<template>
	<Layout>
		<Content>

		</Content>
		<Sider hide-trigger>

		</Sider>
	</Layout>
</template>

<script>
	export default {
		name: 'Problemlist',
		data() {
			return {

			}
		},
		mounted() {
			axios
				.get('http://api.codeoj.cn/home')
				.then(response => {
					this.currentTime = response.data.time
					this.carouseList = response.data.carouse
					this.newsList = response.data.news
					this.$Spin.hide()
				}).catch(function(error) {
					console.log(error);
					this.$Spin.hide()
				});
		}
	}
</script>
</script>

